import { createSlice } from '@reduxjs/toolkit'

// tat initial du slice de rduction pour la gestion du profil utilisateur.
const initialState = {
  isLoading: false,    // Indique si une opration lie au profil est en cours.
  firstName: '',       // Prnom de l'utilisateur.
  lastName: '',        // Nom de l'utilisateur.
  error: '',           // Stocke les ventuelles erreurs lies au profil.
}

// Cration du slice de rduction pour la gestion du profil utilisateur.
const profileSlice = createSlice({
  name: 'profile',      // Nom du slice.
  initialState,         // tat initial dfini prcdemment.
  reducers: {
    // Reducer pour indiquer le dbut d'une opration lie au profil.
    profilePending: (state) => {
      state.isLoading = true
    },
    // Reducer pour mettre  jour le prnom dans l'tat du profil.
    profileFirstName: (state, action) => {
      state.isLoading = false
      state.firstName = action.payload
      state.error = ''    // Rinitialise toute erreur prcdente.
    },
    // Reducer pour mettre  jour le nom de famille dans l'tat du profil.
    profileLastName: (state, action) => {
      state.isLoading = false
      state.lastName = action.payload
      state.error = ''    // Rinitialise toute erreur prcdente.
    },
    // Reducer pour indiquer une erreur lie au profil.
    profileError: (state, action) => {
      state.isLoading = false
      state.error = action.payload
    },
    // Reducer pour dconnecter l'utilisateur et rinitialiser les informations du profil.
    profileOut: (state) => {
      state.isLoading = false
      state.firstName = ''
      state.lastName = ''
      state.error = ''
    },
  },
})

// Extraction des actions et du rducteur  partir du slice cr.
const { actions, reducer } = profileSlice

// Extraction des actions spcifiques du slice.
export const {
  profilePending,
  profileFirstName,
  profileLastName,
  profileError,
  profileOut,
} = actions

// Export du rducteur pour tre utilis dans le store Redux.
export default reducer
