import { Navigate, Outlet } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import { logingSuccess } from '../pages/loginPage/loginSlice'

/**
 * Composant - PrivateRoute
 * Ce composant gre la navigation prive en vrifiant l'authentification de l'utilisateur.
 */

function PrivateRoute() {
  // Rcupration de la fonction dispatch depuis le hook useDispatch de react-redux.
  const dispatch = useDispatch()

  // Rcupration du jeton d'authentification depuis le stockage local.
  const token = localStorage.getItem('token')

  // Garde l'utilisateur connect lors du rafrachissement de la page.
  if (token) {
    // Si un jeton est prsent, dclenche l'action de connexion russie.
    dispatch(logingSuccess())
  }

  // Slection de l'tat d'authentification depuis le store Redux.
  const { isAuth } = useSelector((state) => state.login)

  // Renvoie le composant Outlet si l'utilisateur est authentifi, sinon redirige vers la page d'accueil.
  return isAuth ? <Outlet /> : <Navigate to="/" />
}

export default PrivateRoute
