import { createSlice } from '@reduxjs/toolkit'

// tat initial du slice de rduction pour la gestion de l'authentification.
const initialState = {
  isLoading: false,    // Indique si une opration d'authentification est en cours.
  isAuth: false,       // Indique si l'utilisateur est authentifi.
  isRemember: false,   // Indique si l'utilisateur a choisi l'option "Se souvenir de moi".
  error: '',           // Stocke les ventuelles erreurs lies  l'authentification.
}

// Cration du slice de rduction pour la gestion de l'authentification.
const loginSlice = createSlice({
  name: 'login',        // Nom du slice.
  initialState,         // tat initial dfini prcdemment.
  reducers: {
    // Reducer pour indiquer le dbut d'une opration d'authentification.
    logingPending: (state) => {
      state.isLoading = true
    },
    // Reducer pour indiquer la russite de l'authentification.
    logingSuccess: (state) => {
      state.isLoading = false
      state.isAuth = true
      state.error = ''    // Rinitialise toute erreur prcdente.
    },
    // Reducer pour indiquer une erreur lors de l'authentification.
    logingError: (state, action) => {
      state.isLoading = false
      state.error = action.payload
    },
    // Reducer pour grer le choix "Se souvenir de moi".
    logingRemember: (state, action) => {
      state.isRemember = action.payload
    },
    // Reducer pour dconnecter l'utilisateur.
    logingOut: (state) => {
      state.isAuth = false
    },
  },
})

// Extraction des actions et du rducteur  partir du slice cr.
const { actions, reducer } = loginSlice

// Extraction des actions spcifiques du slice.
export const {
  logingPending,
  logingSuccess,
  logingError,
  logingOut,
  logingRemember,
} = actions

// Export du rducteur pour tre utilis dans le store Redux.
export default reducer
